.domokun{
    -webkit-animation: domokun 2.5s infinite linear;
            animation: domokun 2.5s infinite linear;
    height: 22px;
    position: absolute;
    -webkit-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
    width: 22px;
}
.domokun img{
    height: 20px;
    -o-object-fit: contain;
       object-fit: contain;
}

@-webkit-keyframes domokun{
    0%{-webkit-transform: scale(1.2);transform: scale(1.2)}
    25%{-webkit-transform: scale(1.6);transform: scale(1.6)}
    50%{-webkit-transform: scale(1.75);transform: scale(1.75)}
    75%{-webkit-transform: scale(1.6);transform: scale(1.6)}
    100%{-webkit-transform: scale(1.2);transform: scale(1.2)}
}

@keyframes domokun{
    0%{-webkit-transform: scale(1.2);transform: scale(1.2)}
    25%{-webkit-transform: scale(1.6);transform: scale(1.6)}
    50%{-webkit-transform: scale(1.75);transform: scale(1.75)}
    75%{-webkit-transform: scale(1.6);transform: scale(1.6)}
    100%{-webkit-transform: scale(1.2);transform: scale(1.2)}
}



